import { getActClass } from './utils';
import { LOGIN_CONFIG } from './config';


export default {
  name: 'PressMobileLogin',
  props: {
    showQQ: {
      type: Boolean,
      default: true,
    },
    showWX: {
      type: Boolean,
      default: true,
    },
    currentUrl: {
      type: String,
      default: '',
    },
    loader: {
      type: Function,
      default: null,
    },
    showChooseDialog: {
      type: Boolean,
      default: false,
    },
    showScanDialog: {
      type: Boolean,
      default: false,
    },
    showTipDialog: {
      type: Boolean,
      default: false,
    },
    destroyedWhenClosed: {
      type: Boolean,
      default: false,
    },
    scanCodeType: {
      type: String,
      default: 'WX',
    },
    tipTitle: {
      type: String,
      default: '微信中不支持QQ区账号登录',
    },
    tipImage: {
      type: String,
      default: 'https://image-1251917893.file.myqcloud.com/tipcomm/public_dialog-img/wx-zy.jpg',
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    watchCurrentUrl: {
      type: Boolean,
      default: true,
    },
  },
  emits: [
    'dialogVisibleEvent',
    'jumpToLoginQQ',
    'jumpToLoginWX',
    'update:showChooseDialog',
    'update:showScanDialog',
    'update:showTipDialog',
  ],
  data() {
    return {
      qrcodeSrc: '',
      hideCanvas: false,
      LOGIN_CONFIG,
    };
  },
  watch: {
    currentUrl: {
      handler() {
        if (!this.watchCurrentUrl) {
          return;
        }

        const qrCodeId = document.getElementById('qrcodeid');
        if (qrCodeId) {
          qrCodeId.innerHTML = '';
        }
        this.init();
      },
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {


      this.loader('https://image-1251917893.file.myqcloud.com/commjs/qrcode.min.js', () => {
        // eslint-disable-next-line no-undef
        new QRCode('qrcodeid', {
          // text: this.getCurrentUrl(),
          text: this.currentUrl,
          width: 180,
          height: 180,
          // eslint-disable-next-line no-undef
          correctLevel: QRCode.CorrectLevel.M,
        });
        // 修复android浏览器canvas挡住无法保存图片问题
        const qrcode = window.document.getElementById('qrcodeid');
        if (qrcode) {
          console.log('qrcode', qrcode);
          const canvas = qrcode.getElementsByTagName('canvas');
          if (canvas && canvas.length > 0 && canvas[0]) {
            this.qrcodeSrc = canvas[0].toDataURL('image/png');
            this.hideCanvas = true;
          }
        }
      });

    },
    closeDialog() {
      this.$emit('dialogVisibleEvent', false);
      this.$emit('update:showChooseDialog', false);
      this.$emit('update:showScanDialog', false);
      this.$emit('update:showTipDialog', false);

      if (this.destroyedWhenClosed) {
        const dialogId = 'tip-dialog-login';
        const oldDialog = document.getElementById(dialogId);
        if (oldDialog) {
          oldDialog.parentNode.removeChild(oldDialog);
        }
      }
    },
    jumpToLoginQQ() {
      this.$emit('jumpToLoginQQ');
    },
    jumpToLoginWX() {
      this.$emit('jumpToLoginWX');
    },
    closeScanDialog() {
      this.$emit('update:showScanDialog', false);
      this.$emit('update:showChooseDialog', true);
    },
    closeTipDialog() {
      this.$emit('update:showTipDialog', false);
      this.$emit('update:showChooseDialog', true);
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
